import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Home',
        component: Home,
        children: [{ //
            path: '/',
            name: 'index',
            component: () =>
                import ('@/components/front/index')
        }, { //
            path: '/index',
            name: 'index',
            component: () =>
                import ('@/components/front/index')
        }, { //
            path: '/life',
            name: 'life',
            component: () =>
                import ('@/components/front/life')
        }, { //
            path: '/photo',
            name: 'photo',
            component: () =>
                import ('@/components/front/photo')
        }, { //it
            path: '/it',
            name: 'it',
            component: () =>
                import ('@/components/front/it')
        }, { //
            path: '/my',
            name: 'my',
            component: () =>
                import ('@/components/front/my')
        }, { //
            path: '/search',
            name: 'search',
            component: () =>
                import ('@/components/front/search')
        }]
    }, {
        path: '/Login',
        name: 'Login',
        // 
        component: () =>
            import ('@/components/Login')
    }, {
        path: '/Template',
        name: 'Template',
        // 
        component: () =>
            import ('@/components/Template')
    }]
})


